.p--lead {
  font-size: 1.375rem;
  line-height: calc(28 / 22);
  font-weight: normal;
  font-family: "FS Albert", Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 400;
}

p {
  color: #333f48;
  font-size: 1rem;
  line-height: calc(22 / 16);
  font-weight: normal;
  font-family: "FS Albert", Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 400;
}

@media print, screen and (min-width: 64em) {
  p {
    font-size: 1.125rem;
    line-height: calc(24 / 18);
  }
}

.p--small {
  font-size: 0.875rem;
  line-height: calc(18 / 14);
  font-weight: normal;
  font-family: "FS Albert", Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 400;
}

.p--x-small {
  font-size: 0.75rem;
  line-height: calc(16 / 12);
  font-weight: normal;
  font-family: "FS Albert", Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 400;
}

::selection {
  background: #009ede;
  color: #fff;
}

p {
  margin: 0;
}

.p--tint {
  color: #70a400;
}
